<template>
 
  <view class="container">
    <image src="../../../static/game2.jpg" mode=""></image>
    <div class="card_container">
      <div class="card" :class="[item.cover?'cover':'']" v-for="item in cardList" :key="item.key" :style="item.style"
        @click="clickCard(item,'true')">{{item.content.name}}</div>
    </div>

    <div class="three_card_container">
      <div class="card" v-for="item in saveList" :key="item.key" :style="item.style" @click="clickCard(item,'false')">
        {{item.content.name}}</div>
    </div>
    <div class="choose_card_container">
      <div class="card" v-for="item in penddingList" :key="item.key" :style="item.style">{{item.content.name}}</div>
    </div>
    <div class="tool">
      <button class="btn" @click="removeThree">ÂèñÂá∫‰∏âÂº†Âç°Áâá</button>
      <button class="btn" @click="random">ÈöèÊú∫</button>
      <button class="btn" @click="again">ÂÜçÊù•‰∏ÄËΩÆ</button>
    </div>
    </div>

  </view>
</template>

<script>
  // ÂºïÂÖ•ÂûÉÂúæÊÄªÂíåÁöÑÊï∞ÁªÑ
  import contentType from '../../../tool/contentType.js'
  class Card {
    // Âç°ÁâáÁöÑÂÆΩÈ´ò,Âè™ÊúâÂéüÊú¨È´òÂ∫¶ÁöÑ‰∏ÄÂçäÔºåÂõ†‰∏∫‰πãÂêé‰∏Ä‰∏™Âç°ÁâáÂç†2*2ÁöÑÊï∞ÁªÑÂ§ßÂ∞èÔºåÊñπ‰æøÂΩ¢Êàê‰∏äÂ±ÇÂç°ÁâáÂéãÂà∞‰∏ãÂ±ÇÂç°ÁâáÁöÑÊüê‰∏™ËßíÁöÑÊïàÊûú„ÄÇ
    static x = 20;
    static y = 25;
    // ÂÅèÁßªÈáè
    static offsetx = 25;
    static offsety = 25;
    static contentType = [];
    constructor({
      x,
      y,
      z,
      key
    }) {
      this.x = x;
      this.y = y;
      this.z = z;
      this.key = key;
      this.val = key;
      this.content = {};
      this.cover = false;
      this.style = `top: ${y*Card.y+Card.offsety}px;left:${x*Card.x+Card.offsetx}px;`
    }
    setVal(val) {
      this.val = val - 1;
      this.content = Card.contentType[val - 1]
      this.style += `${this.content.style}`
    }
    static setContentType(list) {
      Card.contentType = list
    }
    static setCardOffsetX(offset) {
      Card.offsetx = offset * 30
    }
    static setCardOffsetY(offsety){
      Card.offsety=offsety*30
    }
  }
  export default {
    // let innerAudioContext = uni.createInnerAudioContext();
    data() {
      return {
        // innerAudioContext:inner,
        options: {},
        aaa: 'üßª',
        // ÊâÄÊúâÂç°Áâá
        cardList: [],
        // ‰øùÂ≠òÁöÑ‰∏âÂº†Âç°Áâá
        saveList: [],
        // ÈÄâÊã©ÁöÑÂç°Áâá
        penddingList: [],
        // Âà§Êñ≠ÈÅìÂÖ∑ÊòØÂê¶ÂèØ‰ª•‰ΩøÁî®
        tools: {
          three: true,
          random: true,
        },
        // Âà§Êñ≠ÊòØÂê¶‰ª•ÂèäËé∑ËÉú
        winner: false,
        // Êï∞ÁªÑÁöÑÂæàËΩ¥ÂíåÁ∫µËΩ¥
        xUnit: 0,
        yUnit: 0,
        // Áî®‰∫éÂà§Êñ≠ÊòØÂê¶ÊûÑÊàê‰∏âÂº†Áõ∏ÂêåÁöÑÂèØ‰ª•Ê∂àÈô§
        calcValueList: [],
      };
    },
    methods: {
      // ÂàùÂßãÂåñÊ∏∏Êàè
      init() {
        // Ê∏ÖÁ©∫list
        this.cardList = []
        this.penddingList = []
        this.saveList = []
        // ÈáçÁΩÆtools
        this.tools.three = true
        this.tools.random = true
        // ÁªòÂà∂Âç°ÁâáÂú∞Âõæ
        this.getMaps()
      },
      // ÁªòÂà∂Âç°ÁâáÂú∞Âõæ
      getMaps() {
        // ÂàùÂßãÂåñÁΩëÁªúÂú∞Âõæ
        let cardMap = this.initMap()
        // Ê†πÊçÆÂç°ÁâáÂØÜÂ∫¶ÔºåÂêëcardmap‰∏≠ÊîæÂÖ•card,Ê≠§Êó∂ÁöÑÂç°ÁâáÊ≤°ÊúâÂÜÖÂÆπ
        cardMap = this.setCard(cardMap)
        // ËÆæÁΩÆÂç°ÁâáÁöÑÂÜÖÂÆπ
        this.setContent()
        // ËÆ°ÁÆóÂç°ÁâáÁöÑÈÅÆÁΩ©ÂÖ≥Á≥ª
        this.calcCover(cardMap)
      },
      clickCard(item, flag) {
        // flagÁî®Êù•Âà§Êñ≠ÁÇπÂáªÁöÑÊòØmap‰∏≠ÁöÑËøòÊòØsaveList‰∏≠ÁöÑ
        if (flag) {
          // Â∞ÜÁÇπÂáªÁöÑÂç°ÁâáÂÜ≤cardList‰∏≠ÂéªÈô§Ôºå‰øùÂ≠òÂú®penddingList‰∏≠
          let index = this.cardList.indexOf(item)
          this.cardList = this.cardList.slice(0, index).concat(this.cardList.slice(index + 1))
        } else {
          // Â∞ÜÁÇπÂáªÁöÑÂç°ÁâáÂÜ≤cardList‰∏≠ÂéªÈô§Ôºå‰øùÂ≠òÂú®penddingList‰∏≠
          let index = this.saveList.indexOf(item)
          this.saveList = this.saveList.slice(0, index).concat(this.saveList.slice(index + 1))
        }
        // ÈáçÊñ∞ËÆæÁΩÆitemÁöÑstyle
        item.style = `left:${(this.penddingList.length-1)*Card.x*2+30*2}px;${item.content.style}`
        this.penddingList.push(item)
        // ÈáçÊñ∞ËÆ°ÁÆóÈÅÆÊå°ÂÖ≥Á≥ª
        this.calcCover()
        //Âà§Êñ≠ÊòØÂê¶Êúâ‰∏â‰∏™ÈáçÂ§çÁöÑÂèØ‰ª•Ê∂àÈô§
        this.cancelCard()
        setTimeout(() => {
          // Âà§Êñ≠ÊòØÂê¶ÊàêÂäüÊàñÂ§±Ë¥•
          this.isWin()
        }, 300)
      },
      // Âà§Êñ≠ÊòØÂê¶ÊàêÂäüÊàñÂ§±Ë¥•
      isWin() {
        if (this.penddingList.length === 7) {
          uni.navigateTo({
            url: '/pages/game/gameResult/gameResult?winner=false'
          })
        } else if (this.cardList.length === 0) {
          uni.navigateTo({
            url: '/pages/game/gameResult/gameResult?winner=true'
          })
        }
      },
      // ‰∏â‰∏™ÈáçÂ§çÁöÑÂèØ‰ª•Ê∂àÈô§
      cancelCard() {
        this.calcValueList = new Array(this.options.maxCard).fill(0)
        this.penddingList.some(item => {
          this.calcValueList[item.val]++
          if (this.calcValueList[item.val] === 3) {
            // ËÆæÁΩÆÂÆöÊó∂Âô®ÔºåÂª∂Âêé‰∏ÄÁÇπËøõË°åÊ∏ÖÈô§,‰ΩøÁ¨¨‰∏âÂº†Âç°ÁâáÊòæÁ§∫Âá∫Êù•
            setTimeout(() => {
              // Ê∏ÖÈô§penddinglist‰∏≠ÂØπÂ∫îÁöÑÂç°Áâá
              this.penddingList = this.penddingList.filter(e => {
                return e.val !== item.val
              })
              // ÈáçÊñ∞Êõ¥Êñ∞penddingList‰∏≠ÁöÑÂç°ÁâáÊ†∑Âºè
              this.penddingList = this.penddingList.map((e, index) => {
                e.style = `left:${(index-1)*Card.x*2+30*2}px;${e.content.style}`
                return e
              })
            }, 300)
          }
        })
      },
      // ËÆ°ÁÆóÂç°ÁâáÈÅÆÁΩ©ÂÖ≥Á≥ª
      calcCover() {
        // ‰ªéÂêéÂæÄÂâçÔºåÂêéÈù¢ÁöÑÂ±ÇÊï∞È´ò
        // ÂàùÂßãÂåñcoverÊï∞ÁªÑÔºåfalseË°®Á§∫Êîπ‰ΩçÁΩÆÊ≤°ÊúâÂç°ÁâáÔºåtrueË°®Á§∫ËØ•‰ΩçÁΩÆÁöÑ‰∏äÊñπÊúâÂç°ÁâáÔºåÈúÄË¶ÅËÆæÁΩÆcoverÊ†∑Âºè
        let coverMap = new Array(this.yUnit)
        for (let i = 0; i < this.yUnit; i++) {
          coverMap[i] = new Array(this.xUnit).fill(false)
        }
        for (let i = this.cardList.length - 1; i >= 0; i--) {
          const item = this.cardList[i]
          const {
            x,
            y
          } = item
          if (coverMap[y][x]) {
            item.cover = true
          } else if (coverMap[y + 1][x]) {
            item.cover = true
          } else if (coverMap[y][x + 1]) {
            item.cover = true
          } else if (coverMap[y + 1][x + 1]) {
            item.cover = true
          } else {
            item.cover = false
          }
          coverMap[y][x] = true
          coverMap[y + 1][x] = true
          coverMap[y][x + 1] = true
          coverMap[y + 1][x + 1] = true

        }
      },
      // ËÆæÁΩÆÂç°ÁâáÁöÑÂÜÖÂÆπ
      setContent() {
        const {
          maxCard
        } = this.options
        const valStack = new Array(maxCard)
        // ÁªôÂç°ÁâáËÆæÁΩÆÂÄº
        this.cardList.forEach((item) => {
          // ÈöèÊú∫‰∏Ä‰∏™ÁßçÁ±ª
          const value = Math.ceil(Math.random() * maxCard);
          if (valStack[value]) {
            // Â∞ÜitemËµãÂÄºËøô‰∏™ÁßçÁ±ªÁöÑÂç°Áâá
            valStack[value].push(item);
            // Â∞Ü‰ªñ‰ª¨ËøõË°å‰∏â‰∏™‰∏â‰∏™ÈÖçÂØπ
            if (valStack[value].length === 3) {
              valStack[value].forEach((item) => {
                item.setVal(value);
              });
              valStack[value] = null;
            }
          } else {
            valStack[value] = [item];
          }
        });
        // ÂØπ‰∏∫ËøõË°åÂàÜÁ±ªÁöÑÈáçÊñ∞ÂàÜÁ±ª
        let count = 2;
        valStack.forEach(list => {
          list && list.map(item => {
            count++
            item.setVal(Math.floor(count / 3))
          })
        })
      },
      // ÂàùÂßãÂåñÂú∞ÂõæÔºåÁ°ÆËÆ§Âç°Áâá‰ΩçÁΩÆ
      setCard(map) {
        const {
          x,
          y,
          z,
          random
        } = this.options
        let key = 0
        const cardList = []
        const shrinkSpeed = 3
        for (let k = 0; k < z; k++) {
          const shrink = Math.floor((z - k - 1) / shrinkSpeed)
          const shrinkX = Math.min(Math.floor(this.xUnit / 2) - 2, shrink)
          const shrinkY = Math.min(Math.floor(this.yUnit / 2) - 2, shrink)
          // Ë°å
          // -1ÊòØÂõ†‰∏∫yÊòØ*2ÂêéÁöÑÔºåÊúÄÂêé‰∏ÄË°å‰∏çËÉΩÊîæÁΩÆÂç°ÁâáÔºå‰∏çÁÑ∂Â∞±‰ºöË∂ÖÂá∫ËæπÁïå
          for (let i = shrinkY; i < this.yUnit - 1 - shrinkY; i++) {
            // Âàó
            for (let j = shrinkX; j < Math.ceil((this.xUnit - 1) / 2); j++) {
              let canSetCard = true
              if (j > 0 && map[k][i][j - 1]) {
                // Â∑¶Ëæπ‰∏çËÉΩÊúâÂç°Áâá
                canSetCard = false
              } else if (i > 0 && map[k][i - 1][j]) {
                // ‰∏äÈù¢‰∏çËÉΩÊúâÂç°Áâá
                canSetCard = false
              } else if (i > 0 && j > 0 && map[k][i - 1][j - 1]) {
                // Â∑¶‰∏äËßí‰∏çËÉΩÊúâÂç°Áâá
                canSetCard = false
              } else if (i > 0 && map[k][i - 1][j + 1]) {
                // Âè≥‰∏äËßí‰∏çËÉΩÊúâÂç°Áâá
                canSetCard = false
              } else if (k > 0 && map[k - 1][i][j]) {
                // Ê≠£Â∫ï‰∏ã‰∏çËÉΩÊúâÂç°Áâá
                canSetCard = false
              } else if (Math.random() > random) {
                canSetCard = false
              }
              // Â¶ÇÊûúËøô‰∏™‰ΩçÁΩÆÂèØ‰ª•ÊîæÁΩÆÂç°ÁâáÔºåÈÇ£‰πàÊ†πÊçÆÈöèÊú∫Êï∞ÔºåËÆæÁΩÆÂç°ÁâáÁöÑÂØÜÂ∫¶
              if (canSetCard) {
                const rand = Math.random()
                const card = new Card({
                  x: j,
                  y: i,
                  z: k,
                  key
                })
                key++
                map[k][i][j] = card
                cardList.push(card)
                // ÂØπÁß∞ÊîæÁΩÆÂç°Áâá
                const mirror = this.xUnit - 2 - j
                if (mirror > j) {
                  const item = new Card({
                    x: mirror,
                    y: i,
                    z: k,
                    key
                  })
                  map[k][i][mirror] = item
                  key++
                  cardList.push(item)
                }
              }

            }
          }
         
        }
        // Âç°ÁâáÂΩ¢Êàê‰∏âÁöÑÂÄçÊï∞,ÂéªÊéâÈ°∂Â±ÇÁöÑÂç°ÁâáÔºåÂõ†‰∏∫È°∂Â±ÇÁöÑÂç°ÁâáÊúÄÂ§ö
        cardList.reverse()
        for (let i = 0; i < key % 3; i++) {
          let item = cardList.pop()
          map[item.z][item.y][item.x] = 0
        }
        cardList.reverse()
        this.cardList = cardList
        console.log('333',this.cardList);
        return map
      },
      // ÂàùÂßãÂåñÁΩëÁªúÂú∞Âõæ
      initMap() {
        const {
          x,
          y,
          z
        } = this.options
        this.xUnit = x * 2
        this.yUnit = y * 2
        const cardMap = new Array(z)
        for (var i = 0; i < z; i++) {
          cardMap[i] = new Array(this.yUnit)
          for (var j = 0; j < this.yUnit; j++) {
            cardMap[i][j] = new Array(this.xUnit).fill(0)
          }
        }
        return cardMap
      },
      // Ê†πÊçÆmaxCardÂàùÂßãÂåñcardÁ±ª‰∏≠ÁöÑcontentTypeÊï∞ÁªÑÔºåÈöèÊú∫Áîü‰∫ßÂûÉÂúæ
      initContentType() {
        const contentList = contentType
        // ÈöèÊú∫Âç°ÁâáÊ†∑ÂºèÊï∞ÁªÑ
        // Ê¥óÁâåÁÆóÊ≥ï
        let shuffle = (arr) => {
          for (let i = arr.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [arr[i], arr[j]] = [arr[j], arr[i]];
          }
          return arr;
        }
        const selected = shuffle(contentList).slice(0, this.options.maxCard)
        Card.setContentType(selected)
      },

      // ÂÜçÊù•‰∏ÄËΩÆ
      again() {
        this.init()
      },
      // ÈöèÊú∫
      random() {
        if (this.tools.random) {
          // ‰ΩøÁî®Ê¥óÁâåÁÆóÊ≥ï
          for (let i = this.cardList.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            let temp = this.cardList[i].content
            this.cardList[i].content = this.cardList[j].content
            this.cardList[j].content = temp
          }
          this.tools.random = false
        } else {
          uni.showToast({
            title: 'ËØ•ÂäüËÉΩÂè™ËÉΩ‰ΩøÁî®‰∏ÄÊ¨°',
            icon: 'none'
          })
        }
      },
      // ÂèñÂá∫‰∏âÂº†Âç°Áâá
      removeThree() {
        if (this.tools.three) {
          // Âà§Êñ≠penddingListÊòØÂê¶Êúâ‰∏âÂº†Âç°Áâá
          let temp;
          if (this.penddingList.length >= 3) {
            for (let i = 0; i < 3; i++) {
              temp = this.penddingList.pop()
              temp.style = `left:${(this.saveList.length-1)*Card.x*2+30*2}px;${temp.content.style}`
              this.saveList.push(temp)
            }
          } else {
            while (this.penddingList.length !== 0) {
              temp = this.penddingList.pop()
              temp.style = `left:${(this.saveList.length-1)*Card.x*2+30*2}px;${temp.content.style}`
              this.saveList.push(temp)
            }
          }
          this.tools.three = false
          console.log('123', this.saveList);
        } else {
          uni.showToast({
            title: 'ËØ•ÂäüËÉΩÂè™ËÉΩ‰ΩøÁî®‰∏ÄÊ¨°',
            icon: 'none'
          })
        }
      }
    },
    onLoad(option) {
      this.options = JSON.parse(option.options)
      if (this.options.x <= 3) {
        Card.setCardOffsetX(8 - this.options.x -1)
        Card.setCardOffsetY(8-this.options.y-1)
      } else {
        Card.setCardOffsetX(8 - this.options.x+1)
        Card.setCardOffsetY(8-this.options.y+1)
      }

      // Ê†πÊçÆmaxCardÂàùÂßãÂåñcardÁ±ª‰∏≠ÁöÑcontentTypeÊï∞ÁªÑÔºåÈöèÊú∫Áîü‰∫ßÂûÉÂúæ
      this.initContentType()
      // ÂàùÂßãÂåñÊ∏∏Êàè
      this.init(this.options)
    
    },
    onShow() {
      this.init()
      this.innerAudioContext = uni.createInnerAudioContext();
      this.innerAudioContext.autoplay = true;
     this. innerAudioContext.loop=true;
      this.innerAudioContext.src = "https://bjetxgzv.cdn.bspapp.com/VKCEYUGU-hello-uniapp/2cc220e0-c27a-11ea-9dfb-6da8e309e0d8.mp3";
      this.innerAudioContext.onPlay(() => {
        console.log('ÂºÄÂßãÊí≠Êîæ');
      });
     this. innerAudioContext.onError((res) => {
        console.log(res.errMsg);
        console.log(res.errCode);
      });
    },
    onHide() {
      this.innerAudioContext.pause()
    },
    onUnload() {
      this.innerAudioContext.pause()
    }

  }
</script>

<style lang="scss">
  .container {
    width: 100vw;
    height: 100vh;
    position: relative;

    image {
      position: absolute;
      width: 100vw;
      height: 100vh;
      opacity: 0.6;
    }

    .card_container {
      position: relative;
      padding: 30px;
      height: 300px;
    }

    .tool {
      position: absolute;
      display: flex;
      width: 100vw;
      bottom: 10px;
      // transform: translate(-50%,0);
      justify-content: space-around;
    }

    .three_card_container {
      position: relative;
      display: flex;
      width: 100%;
      height: 55px;
      padding: 10px 20px;
      top:90px;

      // background-color: green;
      .card {
        margin-right: 7px;
      }
    }

    .choose_card_container {
      position: relative;
      display: flex;
      margin-top: 20px;
      width: 90%;
      left: 10px;
      bottom: -80px;
      height: 70px;
      padding: 10px 15px;
      // background-color: #ccebc6;
      border: 1px solid black;
      border-radius: 20px;
      box-sizing: border-box;
      
      margin: 10px 10px;
      // opacity: 0.3;

      .card {
        margin-right: 7px;
        // opacity: 10;
      }
    }
  }

  .card {
    font-size: 28px;
    text-align: center;
    position: absolute;
    border-radius: 2px;
    box-sizing: border-box;
    background: #ddd;
    opacity: 1;
    cursor: pointer;
    transition: all 0.3s;
    box-shadow: 0px 3px 0 0 #fff, 0 8px 0 0 #ddd, 0 8px 0 2px #333, 0 0 0 2px #333;

    &:hover {
      transform: scale3d(1.1, 1.1, 1.1);
      z-index: 1;
    }
  }

  .cover {
    pointer-events: none;
    box-shadow: 0px 3px 0 0 #999, 0 8px 0 0 #666, 0 8px 0 2px #000, 0 0 0 2px #000;
  }

  .cover:after {
    border-radius: 2px;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: #000;
    opacity: 0.55;
  }
</style>
